.header-color { 
  background: black;
  background: -webkit-linear-gradient(to right,  #3498DB,  #3498DB);  
  background: linear-gradient(to right,  #3498DB,  #3498DB);
}

.about-grid {
  background:#eceff1;
}
.projects-grid {
  background:#eceff1;
}

.about-title {
  font-size: 25px;
  font-weight: bold;
  padding-top: 14px;
  color: #3498DB;
}

.projects-title {
  font-size: 25px;
  font-weight: bold;
  padding-top: 14px;
  color: #3498DB;
}
.about-img {
  height: 450px;
  width: 100%;
  border-radius: 20px;
  margin: auto;
}

.projects-img {
  height: 313px;
  width: 100%;
  border-radius: 20px;
  margin: auto;
}
.banner-text {
  background-color: #3498DB;
  width: 100%;
  margin: auto;
  border-radius: 20px;
  padding-left: 5px;
}

.banner-text h1{
  font-size: 55px;
  font-weight: bold;
  color: white;
  padding-top: 14px;
}

.about-courses {
  background-color: #3498DB;
  width: 100%;
  margin: auto;
  border-radius: 20px;
}

.about-courses li {
  font-size: 17px;
  padding: 5px;
  color: white;
}
.about-courses h3 {
  font-size: 25px;
  font-weight: bold;
  color: white;
  margin: auto;
  padding: 10px;
}

.about-courses h4 {
  font-size: 20px;
  color: white;
  margin: auto;
  padding: 10px;
}

.about-content {
  background-color: #3498DB;
  width: 100%;
  margin: auto;
  border-radius: 20px;
  padding-left: 5px;
}

.about-content h3 {
  font-size: 23px;
  color: white;
  height: 140px;
  text-align: center;
}

.about-contact {
  height: 140px;
  width: 100%;
  border-radius: 20px;
  background-color: #3498DB;
  color: white;
  text-align: center;
}

.about-contact h4 {
  font-size: 20px;
}

.about-contact i {
  font-size: 35px;
  color: white;
}

.projects-content {
  background-color: #3498DB;
  width: 100%;
  margin: auto;
  border-radius: 20px;
}

.projects-content h3 {
  font-size: 23px;
  font-weight: bold;
  color: white;
  margin: auto;

  text-align: center;
}

.projects-content h4 {
  font-size: 23px;
  color: white;
  margin: auto;
  padding:10px;
  text-align:center;
}
.projects-content h5 {
  font-size: 23px;
  color: white;
  margin: auto;

  text-align: center;
}
.projects-content i {
  font-size: 30px;
  color: white;
  margin: auto;
  text-align: center;
  padding:10px;
}

.projects-resume {
  background-color: #3498DB;
  border-radius: 20px;
}

.project2 {
  height:313px;
  width:100%;
  border-radius: 20px;
}

.projects-ppt {
  font-size: 25px;
  font-weight: bold;
